// fetching favorites of logged in user
export async function getFavorites() {
  try {
    const response = await this.axios.get(
      this.$root.store.server_domain + "/users/" + this.$root.store.username + "/favoritesID"
    );
    return response.data;
  } catch (error) {
    console.log(error);
    return {};
  }
}

// fetching viewed recipes of logged in user
export async function getViewed() {
  try {
    const response = await this.axios.get(
      this.$root.store.server_domain + "/users/" + this.$root.store.username + "/view"
    );
    console.log(response);
    return response.data;
  } catch (error) {
    console.log(error);
    return {};
  }
}

// function to check if recipes is in favorites of user
export async function isInFav(recipeId) {
  if (this.$root.store.username) {
    return this.$root.store.favorites.includes(recipeId);
  }
  return false;
}

// function to check if recipe was viewed by user
export async function isInViewed(recipeId) {
  if (this.$root.store.username) {
    return this.$root.store.viewed.includes(recipeId);
  }
  return false;
}

// function that adds a recipe to user's favorite recipes
export async function addToFavorites(recipeId) {
  try {
    console.log("addToFav called");
    const response = await this.axios.post(
      this.$root.store.server_domain + "/users/" + this.$root.store.username + "/favorites",
      { recipeId: recipeId }
    );
    if (response.data === true) {
      console.log("successfully added to favorites");
      return true;
    } else {
      console.error("Failed to add to favorites:", response.message);
      return false;
    } 
  } catch (error) {
    console.log(error);
    return false;
  } 
}